export function getBubbleText(user:number,target:number,newValue:number,oldValue:number)
{
    if (user == target)
    {
        if (newValue>=0.5&&oldValue<0.5)
        {
            return "";
        }
        else if (newValue<=0.5&&oldValue>0.5)
        {
            return "";
        }
        else if (newValue>=-0.5&&oldValue<-0.5)
        {
            return "";
        }
        else if (newValue<=-0.5&&oldValue>-0.5)
        {
            return "";
        }
    }
    if (newValue>=0.5&&oldValue<0.5)
    {
        return "OTHER";
    }
    else if (newValue<=0.5&&oldValue>0.5)
    {
        return "OTHER";
    }
    else if (newValue>=-0.5&&oldValue<-0.5)
    {
        return "OTHER";
    }
    else if (newValue<=-0.5&&oldValue>-0.5)
    {
        return "OTHER";
    }
    return '';
}